import React, { useState } from 'react'
import { useRecoilValue, useSetRecoilState } from 'recoil'
import MenuItem from '@mui/material/MenuItem';
import { useQuery } from '@tanstack/react-query';
import { getClassList } from '../../../api/Class/ClassApi';
import { Select } from '@mui/material';
import dojangIdAtom from '../../../recoil/atom/dojangIdAtom';
import classListAtom from '../../../recoil/atom/classListAtom';
import classCntAtom from '../../../recoil/atom/classCntAtom';


const selectItemStyle = {
  width: '100%',
  position: 'relative',
  display: 'flex',
  alignItems: 'center',
  '&:hover': {
    backgroundColor: '#f3f3f3',
    fontWeight: 'bold',
    '&:before': {
      content: '""',
      backgroundImage: 'url(https://tkdw-img.s3.ap-northeast-2.amazonaws.com/check_select.png)',
      width: '15px',
      height: '15px',
      position: 'absolute',
      right: '10px',
    },
  },
  '&:focus': {
    fontWeight: 'bold',
    '&:before': {
      content: '""',
      backgroundImage: 'url(https://tkdw-img.s3.ap-northeast-2.amazonaws.com/check_select.png)',
      width: '15px',
      height: '15px',
      position: 'absolute',
      right: '10px',
    },
  },
}

const ClassSelectMD = ({ value, onChange, ...props }) => {

  const dojangId = useRecoilValue(dojangIdAtom)
  const setClassList = useSetRecoilState(classListAtom)
  const setClassCnt = useSetRecoilState(classCntAtom)


  // query
  const { data } = useQuery(['getClassList', dojangId], () => getClassList(dojangId), {
    enabled: !!dojangId,
    onSuccess: (res) => {
      setClassList(res)
      setClassCnt(res?.length)
      let l = []
    }
  })
  return (
    <>
      <Select
        sx={{ width: '250px', height: '40px'}}
        placeholder='반 선택'
        value={value}
        onChange={onChange}
        {...props}
      >
        <MenuItem value='0' sx={selectItemStyle}>전체반</MenuItem>
        { data && React.Children.toArray( 
          data?.rows?.map(({id, title}) =>
            <MenuItem value={id} sx={selectItemStyle}
            >
              {title} 
            </MenuItem>
          )
        )}
      </Select>
    </>
  )
}

export default ClassSelectMD